/* eslint linebreak-style: ["error", "windows"] */
import { Navigate } from 'react-router-dom';
import DashboardLayout from 'src/components/DashboardLayout';
import MainLayout from 'src/components/MainLayout';
import Account from 'src/pages/Account';
import CustomerList from 'src/pages/CustomerList';
import Dashboard from 'src/pages/Dashboard';
import Login from 'src/pages/Login';
import NotFound from 'src/pages/NotFound';
import ProductList from 'src/pages/ProductList';
import Register from 'src/pages/Register';
import Settings from 'src/pages/Settings';

const client = process.env.REACT_APP_CLIENT_PATH;
const routes = ({ token, setToken }) => [
  {
    path: `${client}/app`,
    element: token ? <DashboardLayout /> : <Navigate to={`${client}/login`} />,
    children: [
      { path: 'account', element: <Account /> },
      { path: 'customers', element: <CustomerList /> },
      { path: 'dashboard', element: <Dashboard /> },
      { path: 'products', element: <ProductList /> },
      { path: 'settings', element: <Settings /> },
      { path: '*', element: <Navigate to="/404" /> }
    ]
  },
  {
    path: `${client}`,
    element: !token ? <MainLayout /> : <Navigate to={`${client}/app/dashboard`} />,
    children: [
      { path: 'login', element: <Login setToken={setToken} /> },
      { path: 'register', element: <Register /> },
      { path: '404', element: <NotFound /> },
      { path: '/', element: <Navigate to={`${client}/app/dashboard`} /> },
      { path: '*', element: <Navigate to={`${client}/404`} /> }
    ]
  }
];

export default routes;
